import { ReactNode } from 'react';
import {
  Box,
  Flex,
  Avatar,
  HStack,
  Link,
  IconButton,
  Button,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuDivider,
  useDisclosure,
  useColorModeValue,
  Stack,
  Spacer,
} from '@chakra-ui/react';
import { FaMoon,FaSun } from 'react-icons/fa';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';
// import { useColorMode } from '@chakra-ui/react/dist';
import { useColorMode } from "@chakra-ui/color-mode";
import image from "./images/logo.jpg"
const Links = [{a:"Home",class:"nav-link home"},{a:'AboutMe',class:"nav-link about"},{a:"Skills",class:"nav-link skills"},{a:'Projects',class:"nav-link projects"},
 {a:'Contact',class:"nav-link contact"},{a:'Resume',class:"nav-link resume"}];

const NavLink = ({ children }) => (
  <Link
    px={2}
    py={1}
    rounded={'md'}
    _hover={{
      textDecoration: 'none',
      bg: useColorModeValue('gray.200', 'gray.700'),
    }}
  >
    {children}
  </Link>
);

export default function Navbar() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { colorMode, toggleColorMode } = useColorMode();
  const isDark = colorMode === "dark";

  return (
    <>
      <Box    id="nav-menu"  bg={useColorModeValue('#77bbad', '#557A95')} px={4}>
        <Flex h={16} alignItems={'center'} justifyContent={'space-between'}>
        <Flex alignItems={'center'}>
            <Menu>
              <MenuButton
                as={Button}
                rounded={'full'}
                variant={'link'}
                cursor={'pointer'}
                minW={0}>
                  <Flex>
                <Avatar
                  size={'sm'}
                  src={
                   image
                  }
           
                />
                <Spacer/>
                <Box ml="10px" mt="5px"><span style={{ color:"#3FEEE6" ,fontSize:"20px"}}>{"<S"}</span><span style={{color:"#bd651e",fontSize:"20px"}}>{"O"}</span>
                <span style={{color:"#D79922",fontSize:"20px"}}>{"N"}</span><span style={{color:"#3FEEE6",fontSize:"20px"}}>{"U/>"}</span></Box></Flex>
              </MenuButton>
              {/* <MenuList>
                <MenuItem>Link 1</MenuItem>
                <MenuItem>Link 2</MenuItem>
                <MenuDivider />
                <MenuItem>Link 3</MenuItem>
              </MenuList> */}
            </Menu>
          </Flex>
          <IconButton
            size={'md'}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={'Open Menu'}
            display={{ md: 'none' }}
            onClick={isOpen ? onClose : onOpen}
          />
          <HStack spacing={8} alignItems={'center'}>
          <IconButton ml={8} icon={isDark ? <FaSun /> : <FaMoon />} isRound='true' onClick={toggleColorMode}></IconButton>
            <HStack
              as={'nav'}
              spacing={4}
              display={{ base: 'none', md: 'flex' }}>
              {Links.map((link) => (
                <Link href={link.a}  className={link.class}  _hover={{bg:"#282c34",borderRadius:"20%",textDecoration:"none"}} key={link.a}>{link.a}</Link>
              ))}
            </HStack>
          </HStack>
         
        </Flex>

        {isOpen ? (
          <Box pb={4} display={{ md: 'none' }}>
            <Stack as={'nav'} spacing={4}>
              {Links.map((link) => (
                <Link href={link.a}  className={link.class} key={link.a} >{link.a}</Link>
              ))}
            </Stack>
          </Box>
        ) : null}
      </Box>

      <Box p={4}>Main Content Here</Box>
    </>
  );
}